.VerticalContent {
  display: flex;
  position: relative;
  flex-flow: row wrap;
  justify-content: center;
  overflow-y: hidden; }
  .VerticalContent > div {
    margin: 10px;
    padding: 5px;
    background-color: #484848;
    text-shadow: 1px 1px #000; }
    .VerticalContent > div > a {
      display: flex;
      position: relative;
      flex-direction: column;
      color: #FFF;
      max-width: 300px;
      box-sizing: border-box;
      text-decoration: none; }
      .VerticalContent > div > a > img {
        width: 100%;
        height: 150px;
