        margin: 5px auto;
        box-shadow: 0 4px 2px -2px rgba(0, 0, 0, 0.4); }
      .VerticalContent > div > a label:first-child {
        padding: 6px;
        margin: 2px;
        text-shadow: 2px 2px #000;
        font-size: 1.3em;
        cursor: pointer; }
      .VerticalContent > div > a > .details {
        display: flex;
