        width: 100%;
        padding: 10px;
        margin: 2px auto;
        border-top: 2px solid #a3092a; }
        .VerticalContent > div > a > .details > .catalogDetails {
          display: flex;
          flex-flow: column;
          justify-content: flex-end;
          width: 100%;
          font-size: 1.24em;
          text-shadow: 2px 2px 2px #000; }
          .VerticalContent > div > a > .details > .catalogDetails > div:first-child {
